import '../sass/index.scss';
import { useEffect, useState } from 'react';
import About from './About/About';
import Header from './Header/Header';
import CardsList from './CardsList/CardsList';
import Form from './Form/Form';
import UserRegistered from './UserRegistered/UserRegistered';
import * as testApi from 'services/api/testApi';

export const App = () => {
  const [positions, setPositions] = useState([]); //  input type=['radio]     get(/positions)
  const [cards, setCards] = useState([]); //           get(/users)
  const [listInfo, setListInfo] = useState({}); //   (       )
  const [currentPage, setCurrentPage] = useState(1); //          
  const [userId, setUserId] = useState(null); //     .         id   

  const [currentUser, setCurrentUser] = useState({});
  // ========================== Loading and Errors in Promise ==================================
  const [isLoading, setIsLoading] = useState(false);
  const [isLoadingPositions, setIsLoadingPositions] = useState(false);
  const [isLoadingUserInfo, setIsLoadingUserInfo] = useState(false);
  const [error, setError] = useState(null);
  const [errorPositions, setErrorPositions] = useState(null);
  const [errorUserInfo, setErrorUserInfo] = useState(null);
  //=============================================================================================

  useEffect(() => {
    (async function fetchPositions() {
      setIsLoadingPositions(true);
      try {
        const data = await testApi.getPositions();
        setPositions(data.positions);
      } catch (error) {
        setErrorPositions(error.message);
      } finally {
        setIsLoadingPositions(false);
      }
    })(); //   (Immediately Invoked Function Expression)
  }, []); //        input type='radio'

  useEffect(() => {
    (async function fetchUsers() {
      setIsLoading(true);
      setError(false);
      try {
        const { users, page, total_pages, total_users, count, links } = await testApi.getUsers();
        setCards(users);
        setListInfo({
          page,
          total_pages,
          total_users,
          count,
          links,
        });
      } catch (error) {
        setError(error.message);
      } finally {
        setIsLoading(false);
      }
    })(); // IIFE
  }, [userId]); // useEffect        (       )

  useEffect(() => {
    if (!userId) return;
    (async function fetchUsers() {
      setIsLoadingUserInfo(true);
      try {
        const { user } = await testApi.getUserById(userId);
        setCurrentUser(user);
      } catch (error) {
        setErrorUserInfo(error.message);
      } finally {
        setIsLoadingUserInfo(false);
      }
    })(); // IIFE
  }, [userId]); // useEffect    

  const addUser = async formData => {
    setIsLoading(true);
    try {
      await testApi.getToken(); //      headers
      const data = await testApi.addUser(formData); //   

      setUserId(data.user_id); //  id    
    } catch (error) {
      setError(error.message);
    } finally {
      setIsLoading(false);
    }
  };
  const moveToNextPage = async () => {
    setIsLoading(true);
    setError(false);
    try {
      const { users, page, total_pages, total_users, count, links } = await testApi.getUsers(
        currentPage + 1
      ); //     1  
      setCards(users); //     
      setListInfo({ page, total_pages, total_users, count, links }); //     
      setCurrentPage(currentPage + 1); //    
    } catch (error) {
      setError(error.message);
    } finally {
      setIsLoading(false);
    }
  };

  const moveToPrevPage = async () => {
    setIsLoading(true);
    setError(false);
    try {
      const { users, page, total_pages, total_users, count, links } = await testApi.getUsers(
        currentPage - 1
      ); //     1  
      setCards(users); //     
      setListInfo({ page, total_pages, total_users, count, links }); //     
      setCurrentPage(currentPage - 1); //    
    } catch (error) {
      setError(error.message);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div style={{ paddingBottom: 100 }}>
      <Header currentUser={currentUser} error={errorUserInfo} isLoading={isLoadingUserInfo} />
      <main>
        <About />
        <CardsList
          title="Working with GET request"
          cards={cards}
          moveToNextPage={moveToNextPage}
          moveToPrevPage={moveToPrevPage}
          isBtnMoveToNext={listInfo?.links?.next_url}
          isBtnMoveToPrev={listInfo?.links?.prev_url}
          error={error}
          isLoading={isLoading}
        />
        <Form
          title="Working with POST request"
          onSubmit={addUser}
          positions={positions}
          isLoading={isLoadingPositions}
          errorPositions={errorPositions}
          userId={userId}
        />
        {userId && <UserRegistered />}
      </main>
    </div>
  );
};
